import successImg from "../../assets/ok.png";
export default {
  data() {
    return {
      successImg: successImg,
      num:3
    };
  },
  mounted(){
    let timer = setInterval(() => {
        if (this.num <=1) {
          clearInterval(timer);
          this.$router.push("/login");
        } else {
          this.num--;
        }
      }, 1000);
  }
};



